.Header {
  font-family: 'SimSun';
  text-align: center;
  height: inherit;
  line-height: 64px;
  width: 100%;
  display: inline-block;
}
.Header .Header-top {
  height: 64px;
  background: -webkit-gradient(linear, right top, left top, from(#1065d3), color-stop(#5ca9fc), color-stop(#1eb3f8), to(#0376f9));
  background: -webkit-linear-gradient(right, #1065d3, #5ca9fc, #1eb3f8, #0376f9);
  background: linear-gradient(-90deg, #1065d3, #5ca9fc, #1eb3f8, #0376f9);
}
.Header .Header-top .Header-top_container {
  width: 70%;
  height: inherit;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.Header .Header-top .Header-top_container .Header-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.Header .Header-top .Header-top_container .Header-left .Header_logo {
  font-size: 1.5rem;
  font-family: YouYuan;
  font-weight: bold;
  color: #ffffff;
  letter-spacing: 0.2rem;
}
.Header .Header-top .Header-top_container .Header-left .Header_logo .Header_logo_text {
  font-size: 1.5rem;
  font-family: YouYuan;
  font-weight: bold;
}
.Header .Header-top .Header-top_container .Header-left .Header-left-img1 {
  width: 2rem;
  margin-right: 1.13rem;
}
.Header .Header-top .Header-top_container .Header-left .Header-left-spans {
  margin-left: 2.3rem;
  color: #fff;
  font-size: 1rem;
}
.Header .Header-top .Header-top_container .Header-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-input {
  width: 26rem;
  margin-right: 1rem;
  vertical-align: middle;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-input ::-moz-placeholder {
  color: #fff;
  font-size: 0.8rem;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-input ::-webkit-input-placeholder {
  color: #fff;
  font-size: 0.8rem;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-input :-ms-input-placeholder {
  color: #fff;
  font-size: 0.8rem;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-input input {
  background: rgba(0, 0, 0, 0);
  border: 0.1rem solid #18c1fc;
  color: #fff;
  font-size: 0.8rem;
  height: 2rem;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-input button {
  background: #18c1fc;
  border: none;
  height: 2rem;
}
.Header .Header-top .Header-top_container .Header-right .Header-right-name {
  color: #fff;
  font-size: 1rem;
}
.Header .Header-top .Header-top_container .Header-right .Header-rights {
  float: right;
}
.Header .Header-top .Header-top_container .Header-right .Header-rights .Header-right-name {
  color: #fff;
  font-size: 1rem;
}
.Header .Header-Nav {
  height: 46px;
  background: #fff;
  text-align: left;
}
.Header .Header-Nav .Header-Nav_container {
  width: 100%;
  margin: auto;
}
.Header .Header-Nav .Header-Nav_container .Header_nav_menu .ant-menu {
  padding-left: 18%;
}
.Header .Header-Nav .Header-Nav_container .Header_nav_menu .ant-menu .ant-menu-item {
  padding: 0px 1rem;
  margin-right: 5rem;
  font-size: 1.13rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #2B2A2A;
}
.Header .Header-Nav .Header-Nav_container .Header_nav_menu .ant-menu-horizontal .ant-menu-item-selected {
  border-bottom: 2px solid #FCB316;
  color: #026DFF;
}
:global .ant-input {
  border-radius: 2px;
}
.hovers {
  width: 5rem;
  height: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  border-radius: 1px;
  display: inline-block;
}
.hovers:hover {
  background-color: #2d95e7;
}
